module.exports = [{"id":10001,"title":"浅谈：CSS-Flexbox布局","datetime":"2018-1-24 7:33 PM","content":"![cover.jpg](http://upload-images.jianshu.io/upload_images/8017344-36cf42b752298e3d.jpg?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n## 前戏\n\n在正式开始接触弹性盒子布局之前，我们先稍微写一点代码初步感受一下。\n\n``` html\n<div class=\"container\">\n    <div class=\"box\"></div>\n</div>\n```\n\n``` css\n.container {\n    min-height: 100vh;\n    display: flex;\n}\n\n.box {\n    width: 150px;\n    height: 150px;\n    background: #ffaaaa;\n    margin: auto;\n}\n```\n\n![flexbox居中显示.png](http://upload-images.jianshu.io/upload_images/8017344-dc278f3cd54115ac.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n在这里，我们创建了一个容器和其内部的方块，并将容器display属性设置为flex。可以看到，对容器内的方块，我们只是添加了 **margin: auto;** 这一行代码，便实现了其相对容器水平与垂直上的完全居中。也就是说，flexbox首先便解决了css一直以来在垂直居中上的痛点。\n\n## Flexbox简介\n\n先丢一张MDN上面关于弹性盒布局的介绍图\n\n![](http://upload-images.jianshu.io/upload_images/8017344-6b31409ba46fba6c.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n没看懂的话没有关系，这里是[MDN的官方文档](https://developer.mozilla.org/zh-CN/docs/Web/CSS/CSS_Flexible_Box_Layout/Using_CSS_flexible_boxes)，认真看。\n以上\n\n---\n\n。。。\n啊不是，没看懂的话接着往下看。\n\n## Flexbox示例\n\n要使用弹性盒布局，首先便要定义一个弹性容器（flex container）。弹性容器可以通过设置display的值为flex或inline-flex定义，其中flex会被外界视为块级元素，而inline-flex则会被视为行内元素。处在弹性容器内部的元素，就被视为弹性项目（flex item）.\n\n#### flex container\n\n在上面的例子中，container元素已经被添加了 **display: flex;** 属性，也就是说，container元素已经作为一个弹性容器存在，而box元素就是一个弹性项目。\n基于上面的例子，我们做一些修改。\n\n- 将box的margin属性删掉，这里暂时用不到它了。\n- 将原本的1个box元素复制为6个.\n\n刷新页面，我们会发现6个box元素挤在了一起，这并不弹性。\n\n在这里，只需要为弹性容器添加一条属性\n\n``` css\n.container {\n    padding: 20px;\n    display: flex;\n    justify-content: space-around;\n}\n```\n\n刷新页面，6个box便已经均匀地分布在这一行上了。\n\n![justify-content.png](http://upload-images.jianshu.io/upload_images/8017344-1bc273ab03899b49.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n在介绍justify-content属性之前，我们先了解一下主轴的概念：\n\n主轴就是弹性项目在弹性容器的排布方向，默认是横向从左到右的，也就是说，在向弹性容器中塞元素的时候，默认是从左到右一个一个放好的。 当然，主轴的方向是可变的，可以是从右向左，甚至可以是从上到下的。主轴的方向由属性flex-direction控制。\n\n> flex-direction: row | row-reverse | column | column-reverse\n\n而justify-content属性控制的便是弹性项目在主轴上的对齐方式，之前6个box挤在了一起，是因为该属性默认是向主轴的起点对齐。而后我们将justify-content设置为space-around，作用就是使主轴上的弹性项目均匀分布。\n\n> justify-content: flex-start | flex-end | center | space-between | space-around;\n\n接下来我们向容器内再加一些方块。将原来的6个box再复制一遍，现在容器内有12个方块了。刷新页面，我们发现这些方块又连成了一片，甚至还超出了容器的边界，这并不弹性。\n\n于是我们需要为弹性容器再添加一条属性\n\n``` css\n.container {\n    padding: 20px;\n    display: flex;\n    justify-content: space-around;\n    flex-wrap: wrap;\n}\n\n.box {\n    width: 150px;\n    height: 150px;\n    background: #ffaaaa;\n    margin: 10px 0;\n}\n```\n\n为了便于观察，我们为box添加一点外边距。再次刷新，可以看到多出来的方块排列到了下一行，而且当我们调整浏览器窗口大小时，方块们会自动排列整齐。\n\n![flex-wrap.png](http://upload-images.jianshu.io/upload_images/8017344-bcb1124c8bf08f45.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n在这里我们需要了解关于侧轴的概念：\n\n侧轴是定义弹性容器内是否以多行显示，以及多行的排布方向。侧轴的方向与主轴是垂直的，如果主轴是横向的，那么侧轴就是纵向的，反之亦然。侧轴由属性flex-wrap控制，默认为单行显示，也就是不换行。（主轴与侧轴的属性可以简写为flex-flow。例如 **flex-flow: row wrap;**）。\n\n> flex-wrap: nowrap | wrap | wrap-reverse\n\n侧轴与主轴一样可以定义行在侧轴线的对齐方式，由属性align-content控制。\n\n> align-content: flex-start | flex-end | center | space-between | space-around | stretch;\n\n接着我们修改几个方块的高度，让它们看起来不那么一样。\n\n``` css\n.box:nth-child(1) {\n    height: 200px;\n}\n\n.box:nth-child(2) {\n    height: 350px;\n}\n\n.box:nth-child(1) {\n    height: 250px;\n}\n\n.box:nth-child(4) {\n    height: 300px;\n}\n```\n\n可以看到一行的高度是由这一行内最高的元素决定的，那么怎样让元素相对这一行的高度垂直居中呢。\n\n``` css\n.container {\n    padding: 20px;\n    display: flex;\n    justify-content: space-around;\n    flex-wrap: wrap;\n    align-items: center;\n}\n```\n\n![align-items.png](http://upload-images.jianshu.io/upload_images/8017344-c10dd05eb5328464.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n这样，就控制了每一行的元素都以该行的中间位置对齐。而align-items属性控制的便是行内项目的对齐方式。\n\n> align-items: flex-start | flex-end | center | baseline | stretch\n\n#### flex items\n\n以上介绍的都是为弹性容器定义的属性，而我们同样可以为每个弹性容器分别定义不同的属性。\n\norder：控制弹性项目在弹性容器中布局的顺序。例如我们将原本的最后一个方块的order值设置为-1，因为其他方块的order均为默认值0，则最后一个方块将会排在第一位。\n\n> order: <integer> \n\nalign-self：为单独的伸缩项目定义在行内的对齐方式（对应align-items）\n\n> align-self: auto | flex-start | flex-end | center | baseline | stretch;\n\n最后的三个属性都与弹性项目本身的缩放有关，放在一起来说。\n\n我们将上面的示例再稍微改动一下。首先将方块们都删掉，留下四个就可以。然后为了便于观察，为这四个方块设置不同的背景颜色。最后分别为他们添加flex-grow属性，并设置不同的值，例如：\n\n``` css\n.box:nth-child(1) {\n    background: #ffaaaa;\n    flex-grow: 2;\n}\n\n.box:nth-child(2) {\n    background: #f2ffaa;\n    flex-grow: 1;\n}\n\n.box:nth-child(3) {\n    background: #aaffc4;\n    flex-grow: 4;\n}\n\n.box:nth-child(4) {\n    background: #c8aaff;\n    flex-grow: 3;\n}\n```\n\n刷新页面，我们发现四个方块之间没有了间距，并且似乎都宽了不少。\n\n![flex-grow.png](http://upload-images.jianshu.io/upload_images/8017344-7e3280de4ca03981.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n不难看出，flex-grow属性定义的便是弹性项目的扩展能力。但需要注意的是，flex-grow与其他方块的值是成比例的。例如上述示例中，四个方块的属性值分别为2、1、4、3，总和为10，则方块1扩展能力就是2/10，它能占用的剩余空间就是1/5的大小。而如果只有一个方块定义了flex-grow属性，因为其他方块值均为默认值0（不扩展），那么无论这个方块的flex-grow属性为多少，它都将得到全部的剩余空间。\n\n> flex-grow: <number>; /* default 0 */\n\n与之相对应的，就是flex-shrink属性。该属性定义弹性项目的收缩能力。在容器缩小时随着容器等比例缩小。\n\n> flex-shrink: <number>; /* default 1 */\n\nflex-basis：这个属性定义弹性项目在主轴上所占的长度。很容易理解，与宽度类似，但与宽度不同的是，flex-basis是在**主轴**方向上的长度，也就是说，如果主轴是纵向的，那么它定义的就是高度（可以这样理解）。\n\nflex-grow, flex-shrink, flex-basis可以简写为flex属性，例如 **flex: 2 2 20%;** \n\n## 结语\n\n关于flexbox布局各属性的详细信息如果仍有疑问请手动查阅[MDN文档](https://developer.mozilla.org/zh-CN/docs/Web/CSS/CSS_Flexible_Box_Layout)"},{"id":2,"title":"浅谈：CSS-Grid布局","datetime":"2018-1-25 7:02 PM","content":"![](http://upload-images.jianshu.io/upload_images/8017344-719e5b5ad978fb29.jpg?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n## 前戏\n\n没有前戏。。\n。。。\n真的没有。\n\n## Grid布局简介\n\n不多bb先丢个文档上来（嗯） [网格布局 - CSS | MDN](https://developer.mozilla.org/zh-CN/docs/Web/CSS/CSS_Grid_Layout)。\n\n## Grid布局示例\n\n### 拼方块\n\n要使用网格布局，与flexbox一样，需要先定义一个容器。\n\n> display: grid | inline-grid | subgrid\n\n很简单，定义好容器之后就可以在容器内分割网格了，与table有些类似。\n\n``` html\n<div class=\"container\">\n    <div class=\"item\">1</div>\n    <div class=\"item\">2</div>\n    <div class=\"item\">3</div>\n    <div class=\"item\">4</div>\n    <div class=\"item\">5</div>\n</div>\n```\n\n``` css\n.container {\n    border: solid 1px rgb(142, 150, 255);\n    display: grid;\n    grid-template-columns: repeat(3, 160px);\n    grid-template-rows: repeat(3, 160px);\n}\n```\n\n![grid-template-row(column).png](http://upload-images.jianshu.io/upload_images/8017344-8ef8e48d2a020606.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n这里先创建了1个container和5个放在容器中的item，并且为了便于观察，为container添加了一个边框。而container的gird-template-columns和grid-template-rows就是为容器划分网格的属性。\n\n> grid-template-columns: <track-size> ... | <line-name> <track-size> ...\n   grid-template-rows: <track-size> ... | <line-name> <track-size> ...\n\n这两个属性一个负责划分列，一个负责划分行。格式例如：**grid-template-rows: 50px 100px 150px;**，需要多少行（列）就写多少个值，每个值都代表对应那一行（列）的宽度。\n\n上述代码将容器划分为了3*3、每个单元格的宽高均为160px的网格。\n\n但是这些方块被连成了一片，为了区分他们的边界，这里需要给这些元素添加一点间距。\n\n``` css\n.container {\n    border: solid 2px rgb(142, 150, 255);\n    display: grid;\n    grid-template-columns: repeat(3, 160px);\n    grid-template-rows: repeat(3, 160px);\n    grid-gap: 8px;\n}\n```\n\n![grid-gap.png](http://upload-images.jianshu.io/upload_images/8017344-3464694e7e5319fe.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\ngrid-gap属性负责控制网格线的宽度，可以直接理解为行（列）间距。可以分别控制行间距和列间距。\n\n> grid-column-gap: <line-size>\n> grid-row-gap: <line-size>\n> grid-gap: <grid-row-gap> <grid-column-gap>\n\n现在容器内一共有9个单元格，但只有5个方块，为了让这些方块能够填满这个容器，只需要为每个方块指定分别占用哪些格子即可。\n\n``` css\n.item:nth-child(1) {\n    grid-column: 1 / 3;\n}\n\n.item:nth-child(2) {\n    grid-column: 3 / 4;\n    grid-row: 1 / 3;\n}\n\n.item:nth-child(3) {\n    grid-column: 2 / 4;\n    grid-row: 3 / 4;\n}\n\n.item:nth-child(4) {\n    grid-row: 2 / 4;\n}\n```\n\n![grid-row(column).png](http://upload-images.jianshu.io/upload_images/8017344-2f4d16accb8e79f9.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\ngrid-column和grid-row属性分别指定网格项在列和行上占用的网格区域。例如 **grid-column: 2 / 4;** 指定该网格项在列方向上占用第2条网格线到第4条网格线之间的区域，注意这两个数值指定的是**网格线**而不是单元格（除此之外还可以通过第二个值指定该网格项跨越的单元格数量，默认为1）。\n\n> grid-column: <start-line> / <end-line> | <start-line> / span <value>\n   grid-row: <start-line> / <end-line> | <start-line> / span <value>\n\n同样可以为这两个属性分别设置起点与终点\n\n> grid-column-start: <number> | <name> | span <number> | span <name> | auto\n   grid-column-end: <number> | <name> | span <number> | span <name> | auto\n   grid-row-start: <number> | <name> | span <number> | span <name> | auto\n   grid-row-end: <number> | <name> | span <number> | span <name> | auto\n\n而在划分网格时，可以为网格线定义名称，并使用grid-row(column)属性直接通过名称选择网格线而非通过数字（默认）。例如：\n\n``` css\n.container {\n    grid-template-columns: [line-1] 160px [line2] 160px [line3] 160 [line4] ;\n}\n\n.item:nth-child(1) {\n    grid-column: line1 / line3;\n}\n```\n\n### 实现圣杯布局\n\n到了这里，就可以马上动手用grid实现一个简单的圣杯布局。\n\n![圣杯布局1.png](http://upload-images.jianshu.io/upload_images/8017344-b32992672810cd61.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n很简单，也没什么好说的，主要是熟悉属性用法。\n\n### 再拚一遍方块\n\n除了使用grid-row和grid-column属性为网格项指定占用的网格区域，还可以通过grid-area属性来控制。为了使用这一属性，需要先在容器内定义属性grid-template-areas。\n\n``` css\n.container {\n    display: grid;\n    grid-template-columns: repeat(3, 160px);\n    grid-template-rows: repeat(3, 160px);\n    grid-gap: 8px;\n    grid-template-areas: \n        \"item1 item1 item2\"\n        \"item4 item5 item2\"\n        \"item4 item3 item3\";\n}\n\n.item:nth-child(1) {\n    grid-area: item1;\n}\n\n.item:nth-child(2) {\n    grid-area: item2;\n}\n\n.item:nth-child(3) {\n    grid-area: item3;\n}\n\n.item:nth-child(4) {\n    grid-area: item4;\n}\n```\n\n可以看出grid-template-areas属性定义了可视化的网格结构，为每个单元格设置名称并通过设置网格项的grid-area属性来指定其所占用的网格区域。\n\n得到的结果是一样的，因为grid-template-areas使用可视化语法书写，所以很好理解（可以使用任意数量的相邻的.来声明单个空单元格。 只要这些点号之间没有空格，他们就代表了一个单一的单元格）。\n\n而grid-area除了可以设置网格项对应的名称外，还可以作为grid-row和grid-column的缩写。\n\n> grid-area: <name> | <row-start> / <column-start> / <row-end> / <column-end>\n\n### 其他属性\n\n#### 网格容器属性\n\n**justify-items和align-items**：两个属性一个是定义**单元格内的元素相对单元格**行轴方向的对齐方式，一个是定义列轴方向的对齐方式。与flexbox的属性很相似，不过要注意的是flex是没有justify-items这个属性的。\n\n> justify-items: start | end | center | stretch\n\n> align-items: start | end | center | stretch\n\n**justify-content和align-content**：同样和flexbox的属性相似。一个是定义**单元格在网格容器内**行轴方向的对齐方式，一个是定义列轴方向的对齐方式。\n\n> justify-content: start | end | center | stretch | space-around | space-between | space-evenly\n\n> align-content: start | end | center | stretch | space-around | space-between | space-evenly\n\n**grid-auto-columns和grid-auto-rows**：指定网格的隐式轨道的大小。也就是当网格项的显式定位超出了网格容器的范围，在缺少轨道的位置填充的大小。\n\n``` css\n.container {\n    display: grid;\n    grid-template-columns: repeat(3, 160px);\n    grid-template-rows: repeat(3, 160px);\n    grid-gap: 8px;\n    grid-template-areas: \n        \"item1 item1 item2\"\n        \"item4 item5 item2\"\n        \"item4 item3 item3\";\n    grid-auto-columns: 160px;\n}\n\n.item:nth-child(5) {\n    grid-column: 5 / 6;\n}\n```\n\n![grid-auto-columns.png](http://upload-images.jianshu.io/upload_images/8017344-f3a8841e761069d0.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n这里第5个方块被显式定位到第5和第6条网格线之间，明显超出了容器的范围，于是在第4和第6条网格线之间便存在了2条隐式轨道，而grid-auto-columns就是用来定义这条轨道的宽度。若没有定义该属性，则轨道宽度为0（默认）。\n\n> grid-auto-columns: <track-size>\n   grid-auto-rows: <track-size>\n\n**grid-auto-flow**：用来定义未被定位的网格项的放置规则。很好理解，在最开始的实例中，网格中的5个方块就是横着一行一行排下来的（默认row），如果将这个属性改为column，则会纵向一列一列排过来。\n\n> grid-auto-flow: row | column | row dense | column dense\n\n**grid**：grid-template-rows，grid-template-columns，grid-template-areas，grid-auto-rows，grid-auto-columns和grid-auto-flow的简写。\n\n> grid: none | <grid-template-rows> / <grid-template-columns> | <grid-auto-flow> [<grid-auto-rows> [/ <grid-auto-columns>]]\n\n#### 网格项属性\n\n**justify-self和align-self**：看名字就知道干什么用的了吧。控制单个网格项在单元格内的对齐方式。\n\n> justify-self: start | end | center | stretch\n\n> align-self: start | end | center | stretch\n\n## 结语\n\n关于grid布局各属性的详细信息如果仍有疑问请手动查阅[MDN文档](https://developer.mozilla.org/zh-CN/docs/Web/CSS/CSS_Flexible_Box_Layout)。"},{"id":3,"title":"用Github和Hexo搭建个人博客","datetime":"2018-2-1 20:24 PM","content":"## 前戏\n\n嗯。。\n\n## 创建Github Page仓库\n\n首先需要在github中创建一个名为**你的名字.github.io**的新仓库来存放页面代码。这样在这个仓库中部署的代码可以通过**你的名字.github.io**这个网址来直接访问预览页面，而且以后还可以用自己购买的域名来替换github分配的网址。\n\n## 搭建本地环境\n\nHexo是在本地环境中将markdown文件和主题一起渲染打包，然后发布推送到github上，所以首先需要部署本地环境。\n\n本机安装了node的前提下，打开一个安全的路径，直接逐条执行以下命令。\n\n``` shell\nnpm install -g hexo-cli\nhexo init myBlog\ncd myBlog\nnpm i\n```\n\n然后打开myBlog目录中的_config.yml，稍微做一点配置。\n\n1. 第6行title，改为网站的名字。\n2. 第9行author，改为作者的名字。\n3. 最后一行type改为 `type: git` 。\n4. 最后一行添加 `repo: {你的仓库地址}` 。\n\n配置完成后再执行最后一句 `npm install hexo-deployer-git --save` 。\n\n这样本地环境就搭建好了，很简单，想要看看效果的话执行`hexo deploy`稍等片刻打开github page地址就可以看到搭建好的博客了。\n\n## 发布文章\n\n博客当然是要用来写文章的，而发布文章的流程也同样很简单。\n\n1. 执行 `hexo new 文章名称` 。\n2. 进入source/_posts目录，打开刚刚新建的文章，开始创作。\n3. 写完文章后执行 `hexo generate` 生成修改后的博客页面。\n4. 再执行 `hexo deploy` 就可以发布到github了。\n\n## 修改主题\n\n主题当然也是可以换的。\n\n1. 先从[这里](https://github.com/hexojs/hexo/wiki/Themes)挑一个自己看着顺眼的主题，复制它的clone地址。\n2. 执行 `git clone git@github.com:iissnan/hexo-theme-next.git themes/{主题名字}` 下载主题。\n3. 修改_config.yml文件75行为刚刚下载的主题名字。\n4. 执行 `hexo generate` 和 `hexo deploy` 打包发布。\n\n这样就完成了。\n\n![](http://upload-images.jianshu.io/upload_images/8017344-9ebde19935aa79b5.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n\n\n## 结语\n\n以前没有用过Hexo，尝试了一下发现确实很方便，但是还是觉得少了点什么，毕竟个人主页还是喜欢自己一点一点搭建出来的感觉吧。"},{"id":4,"title":"从输入网址到页面呈现过程浅析","datetime":"2018-2-3 3:38 PM","content":"## 前戏\n\n直接按顺序讲，中间带些概念解释。尽量讲的简短些吧，毕竟懒233。。\n\n![](http://upload-images.jianshu.io/upload_images/8017344-deae9ef90e4dd9bd.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n## 正文\n\n### 1. 输入网址\n\n网址，也就是**[URL（统一资源定位符）](https://zh.wikipedia.org/wiki/%E7%BB%9F%E4%B8%80%E8%B5%84%E6%BA%90%E5%AE%9A%E4%BD%8D%E7%AC%A6)**，用来定位用户想要访问的网页。它的标准格式是这样的：\n\n> 协议类型:[//服务器地址[:端口号]][/资源层级UNIX文件路径]文件名[?查询][#片段ID]\n\n然而一般情况只需要输入域名就可以了，比如直接输入www.baidu.com就可以直接跳转到百度。因为浏览器会自动补全协议类型（http）、端口号（80）和路径（/）。\n\n### 2. 解析域名\n\n单独只靠域名是无法找到对应的服务器的，为了找到服务器，需要先得到服务器的IP地址。这里就需要使用**[DNS（域名系统）](https://zh.wikipedia.org/wiki/%E5%9F%9F%E5%90%8D%E7%B3%BB%E7%BB%9F)**将域名解析为对应的IP地址。而DNS实际上就是一个存储域名与IP相互映射的数据库。\n\n### 3. 浏览器发送请求\n\n拿到IP地址之后浏览器便可以向服务器发送请求。而请求的格式需要遵守一种约定，也就是**[HTTP（超文本传输协议）](https://zh.wikipedia.org/wiki/%E8%B6%85%E6%96%87%E6%9C%AC%E4%BC%A0%E8%BE%93%E5%8D%8F%E8%AE%AE)**，HTTP是客户端和服务器请求和应答的标准，也是必须遵守的规定。这个标准规定了请求大概是这样的：\n\n![](http://upload-images.jianshu.io/upload_images/8017344-36fd18dbdd3adb5a.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n看下第一行就可以了。其中GET表示请求方式，中间的“ / ”表示请求的路径，后面的HTTP/1.1就是HTTP协议版本。这样浏览器就把请求交给了服务器并且等它回应。\n\n### 4. 服务器响应请求\n\n服务器拿到请求之后会根据请求的路径返回不同的文件内容。例如访问某服务器的/index.html路径时，服务器内部就会响应请求并将这个路径对应的文件内容传输回客户端。这里涉及到**[TCP（传输控制协议）](https://zh.wikipedia.org/wiki/%E4%BC%A0%E8%BE%93%E6%8E%A7%E5%88%B6%E5%8D%8F%E8%AE%AE)**，只需要了解HTTP传输是基于TCP即可。\n\n根据HTTP协议，服务器相应返回的结果大概是这样的：\n\n![](http://upload-images.jianshu.io/upload_images/8017344-4db7366bfacf85d3.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n同样只需要看下第一行就好了。中间的200是**[HTTP状态码](https://zh.wikipedia.org/wiki/HTTP%E7%8A%B6%E6%80%81%E7%A0%81)**，200就表示成功，后面的OK跟状态码一起的，用来解释状态码。\n\n（服务器端需要指定Content-Type的值以确定返回文件类型）\n\n### 5. 浏览器解析文件内容\n\n浏览器接收到文件后就开始逐行解析文件的内容。如果文件中又包含另外的请求，如在html文件中遇到link标签，则需要根据指定路径再次发送请求，也就是重复第3到4步。直到所有文件全部解析请求完毕。\n\n### 6. 将请求结果呈现到页面\n\n当所有请求与接收全部完成之后，一个完整的页面就被呈现到了用户的面前。\n\n![](http://upload-images.jianshu.io/upload_images/8017344-f21a0172966a72d8.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n## 结语\n\n没有结语，链接都在正文里了。。"},{"id":5,"title":"三分钟看完CSS盒模型","datetime":"2018-2-6 7:53 PM","content":"## 前戏\n\n很简单的概念，没有理解难点。惯例开场丢图\n\n![CSS盒模型.png](http://upload-images.jianshu.io/upload_images/8017344-4236ecc81ca52a15.png?imageMogr2/auto-orient/strip%7CimageView2/2/w/1240)\n\n## 什么是盒模型\n\n在html中，每个元素都是一个盒子。因为网页一般来说都是二维系统，所以可以想象在页面上观察到的就是这些盒子的俯视图。\n\n上文图中就是一个完整的盒模型。其中的content也就是最中间的那一块就是盒子中的内容；外面border那一圈代表的就是盒壁的厚度，也就是边框；而边框到内容之间的距离，就被称作内边距；最后外面那一圈，被称作外边距，它可以让盒子们不会全部挤到一起，而是保持适当的间距。\n\n也就是说，一个完整的盒模型占据的宽度实际是content宽度+左右padding+左右border+左右margin的总长度，而高度则是content高度+上下padding+上下border+上下margin的总长度。\n\n## box-sizing\n\n那么在css中，width和height指的是谁的宽度和高度呢。\n\n``` css\n.box {\n  width: 200px;\n  height: 200px;\n}\n```\n实际上，直接在css中设置宽高的话设置的是content的宽高。。是的，既不是整个盒模型的宽高，也不是盒子的宽高，而是盒子中的内容的宽高，也就是说上述代码只是指定了content的大小为200px*200px，而如果再给这个元素添加padding和border属性，盒子就会随着变大。\n\n``` css \n.box {\n  padding: 20px;\n}\n```\n\n这里为box元素添加了一个20px的内边距，而content的宽高已经设定好不会再变，那么盒子就向上下左右各扩展20px的距离充当内边距。如果添加边框则是同理。而如果置了背景、颜色或者图片，这些样式将会延伸到padding上。\n\n这就很令人费解了，大部分人第一反应都应该会觉得指定元素的宽高就应该是盒子的宽高，尤其在元素拥有背景或者边框时，就更容易产生错觉。而且当需要调整一个元素的宽度和高度使还需要时刻注意元素的内边距和边框，让人十分困扰。\n\n好在现在这个“错误”已经有了解决方案了，现在只需要给元素添加一个属性\n\n``` css\n.box {\n  box-sizing: border-box;\n}\n```\n\n这样元素的宽高就包含了元素的内边距和边框。如果再给这个元素指定宽度高度以及内边距，那么这个元素的实际内容区的宽度就是width-padding-border。这样就可以更容易地去调整一个元素的宽高。实际开发中可以根据自己的习惯选择不同的盒模型规范。\n\n## 外边距合并\n\n外边距合并就是当两个**垂直边距**相遇时，形成的外边距不是两个外边距的总和，而是两个外边距中的较大值。例如元素1外边距是30px，元素2外边距是20px，元素2处在元素1下方，那么他们之间的间距就是30px。\n\n三种情况会产生外边距合并，稍微记一下就好了。\n\n1. 相邻叠加的两个块级元素，如果它们的上下边距相遇，即会产生叠加。\n2. 块级元素自身没有内容、内边距、边框时，它的上下外边距就会相遇产生叠加。\n3. 块级父元素和其第一个/最后一个子元素之间没有内边距、边框时，它们的边距就会相遇产生叠加。\n\n## 结语\n\n惯例放MDN文档。\n- [盒子模型](https://developer.mozilla.org/zh-CN/docs/Web/CSS/CSS_Box_Model/Introduction_to_the_CSS_box_model)\n- [box-sizing](https://developer.mozilla.org/zh-CN/docs/Web/CSS/box-sizing)\n- [外边距合并](https://developer.mozilla.org/zh-CN/docs/Web/CSS/CSS_Box_Model/Mastering_margin_collapsing)"},{"id":6,"title":"用JavaScript实现常见的排序算法","datetime":"2018-2-10 9:56 PM","content":"##前戏\n\n- 复习了一些比较常见的排序算法，用JS实现，带一些实现思路。\n- 无图，无脑贴代码。。\n\n## 比较排序\n\n### 冒泡排序\n\n1. 比较相邻的元素，若第二个比第一个大，则交换位置。\n2. 对每一对相邻元素作比较，一轮交换完成后，最大的元素应该排在最后，即排序完成。\n3. 对剩余待排序元素重复步骤1~2，直到所有元素都已排序完成。\n\n``` js\nfunction bubbleSort(paramArr) {\n    const arr = paramArr.slice()\n    for (let i = 0; i < arr.length; i++) {\n        for (let j = 0; j < arr.length - i; j++) {\n            if (arr[j] > arr[j + 1]) {\n                const temp = arr[j]\n                arr[j] = arr[j + 1]\n                arr[j + 1] = temp\n            }\n        }\n    }\n    return arr\n}\n```\n\n### 选择排序\n\n1. 从未排序序列中选择最小的元素放在已排序序列的末尾。\n2. 重复步骤1，直到所有元素都已被放入已排序序列。\n\n``` js\nfunction selectionSort(paramArr) {\n    const arr = paramArr.slice()\n    for (let i = 0; i < arr.length; i++) {\n        let minIndex = 0\n        for (let j = 1; j < arr.length - i; j++) {\n            if (arr[j] < arr[minIndex]) {\n                minIndex = j\n            }\n        }\n        arr.push(arr[minIndex])\n        arr.splice(minIndex, 1)\n    }\n    return arr\n}\n```\n\n### 插入排序\n\n1. 取出第一个元素，这个元素可以认为已经被排序。\n2. 取出下一个元素，从后向前依次对比，如果已排序元素大于新元素，则将该元素后移一位。\n3. 重复步骤2，直到找到已排序元素小于等于新元素的位置，将新元素插入。\n4. 重复步骤2~3，直到所有元素都插入完毕。\n\n``` js\nfunction insertionSort(paramArr) {\n    const arr = paramArr.slice()\n    for (let i = 1; i < arr.length; i++) {\n        for (let j = i; j > 0; j--) {\n            if (arr[j - 1] > arr[j]) {\n                const temp = arr[j]\n                arr[j] = arr[j - 1]\n                arr[j - 1] = temp\n            } else {\n                break\n            }\n        }\n    }\n    return arr\n}\n```\n\n### 快速排序\n\n1. 在待排序元素中选择一个元素作为基准。\n2. 将所有小于基准的元素都移到基准的左边，所有大于基准的元素都移到基准的右边。操作结束后，基准就已经处在最终排序后它的位置。\n3. 对左右两个子集重复步骤1~2，直到所有的子集只剩下一个元素。\n\n这里说一下代码细节：\n\n1. 将序列中最后一个数作为基准数。\n2. 用storeIndex表示移动元素放置的位置，最开始指向序列起点。\n3. 从前向后遍历，移动小于等于基准元素的元素到数组的开头，每次移动 storeIndex 自增 1。\n4. 循环结束后将storeIndex位置上的元素与基准数交换，此时一轮排序完成。\n\n``` js\nfunction randomizedQuickSort(paramArr) {\n    const swap = (arr, index1, index2) => {\n        const temp = arr[index1]\n        arr[index1] = arr[index2]\n        arr[index2] = temp\n    }\n\n    const partition = (arr, start, end) => {\n        let storeIndex = start\n        const standardIndex = end\n        for (let i = start; i < end; i++) {\n            if (arr[i] <= arr[standardIndex]) {\n                swap(arr, storeIndex, i)\n                storeIndex += 1\n            }\n        }\n        swap(arr, storeIndex, end)\n        return storeIndex\n    }\n\n    const sort = (arr, start, end) => {\n        if (start > end) return []\n        const storeIndex = partition(arr, start, end)\n        return [\n            ...sort(arr, start, storeIndex - 1),\n            arr[storeIndex],\n            ...sort(arr, storeIndex + 1, end)\n        ]\n    }\n\n    return sort(paramArr.slice(), 0, paramArr.length - 1)\n}\n```\n\n## 非比较排序\n\n### 计数排序\n\n懒。。介绍就抄维基的啦。\n\n1. 找出待排序的数组中最大和最小的元素\n2. 统计数组中每个值为 i 的元素出现的次数，存入数组 C 的第i项\n3. 对所有的计数累加（从 C 中的第一个元素开始，每一项和前一项相加）\n4. 反向填充目标数组：将每个元素i放在新数组的第 C[i] 项，每放一个元素就将 C[i] 减去1\n\n``` js\nfunction countingSort(arr) {\n    const bucketArr = []\n    const sortedArr = []\n\n    for (let i = 0; i < arr.length - 1; i++) {\n        if (!bucketArr[arr[i]]) {\n            bucketArr[arr[i]] = 1\n        } else {\n            bucketArr[arr[i]] += 1\n        }\n    }\n\n    for (let i = 0; i < bucketArr.length - 1; i++) {\n        if (bucketArr[i]) {\n            for (let j = 0; j < bucketArr[i]; j++) {\n                sortedArr.push(i)\n            }\n        }\n    }\n\n    return sortedArr\n}\n```\n\n### 桶排序\n\n1. 设置一个定量的数组当作空桶子。\n2. 寻访序列，并且把项目一个一个放到对应的桶子去。\n3. 对每个不是空的桶子进行排序。\n4. 从不是空的桶子里把项目再放回原来的序列中。\n\n这里对每个桶中的元素使用插入排序\n\n``` js\nfunction bucketSort(arr, step) {\n    const bucket = []\n    const sortedArr = []\n\n    for (let i = 0; i < arr.length; i++) {\n        const index = Math.floor(arr[i] / step)\n        if (!bucket[index]) {\n            bucket[index] = []\n            bucket[index].push(arr[i])\n        } else {\n            bucket[index].push(arr[i])\n            for (let j = bucket[index].length - 1; j > 0; j--) {\n                if (bucket[index][j - 1] > bucket[index][j]) {\n                    const temp = bucket[index][j]\n                    bucket[index][j] = bucket[index][j - 1]\n                    bucket[index][j - 1] = temp\n                } else {\n                    break\n                }\n            }\n        }\n    }\n\n    for (let i = 0; i < bucket.length; i++) {\n        if (bucket[i]) {\n            for (let j = 0; j < bucket[i].length; j++) {\n                sortedArr.push(bucket[i][j])\n            }\n        }\n    }\n\n    return sortedArr\n}\n```\n\n## 结语\n\n还有一些比较常见的如基数排序、堆排序这里没有写（其实是已经忘干净了。。可能以后会再开一篇吧）\n\n惯例贴链接：\n\n- [排序算法 - 维基百科](https://zh.wikipedia.org/wiki/%E6%8E%92%E5%BA%8F%E7%AE%97%E6%B3%95#%E6%8E%92%E5%BA%8F%E7%AE%97%E6%B3%95%E5%88%97%E8%A1%A8)\n- [排序算法的可视化](https://visualgo.net/bn/sorting)\n- [bubkoo](http://bubkoo.com/tags/algorithm/)"}]

